// eslint-disable-next-line no-extend-native
String.prototype.onHighlightText = function (text, callback) {
    if (text) {
        let regex = new RegExp(text.trim().toLowerCase(), "gi");

        let highlightedItem = this.replace(
            regex,
            `<span class='highlight'>${callback ? callback(text) : "$&"}</span>`
        );

        return highlightedItem;
    }
    return this;
};
// eslint-disable-next-line no-extend-native
String.prototype.removeSpecialCharacters = function () {
    const pattern = /[,./;'[\]\=-`\\\/<>?:"{}|+_\(\)*&^%$#@!~]/g; // Biu thc chnh quy  tm cc k t c bit

    return this.replace(pattern, ""); // Loi b cc k t c bit trong chui
};

